<script>
import osc from "osc";
import $ from 'jquery'


 var port = new osc.WebSocketPort({
          url: "wss://circusfamilyprojects.nl:8084" //  ws://localhost:8083 online server wss://circusfamilyprojects.nl:8084
        }); 

 port.open();


export default {
  

  data() {
  return{
    value: 60,
    addressen:[],
    address:'',
    address2:'',
    color:'',
    colum1color1: '#3f87a6',
    colum1color2: '#ebf8e1',
    colum2color1: '#3f87a6',
    colum2color2: '#ebf8e1',
    colum3color1: '#3f87a6',
    colum3color2: '#ebf8e1',
    colum4color1: '#3f87a6',
    colum4color2: '#3f87a6',
    column1_postion: 50,
 
    
    
     
    
  }},
   created: function(){
             this.OSCMessage();      
          //console.log(message);

     },
     components:{
   
     },
  computed: {

    
  },
  methods:{


      OSCMessage: function(){ 
               
        port.on("message", (oscMessage) => {
          
             console.log(oscMessage);
             this.OSCMessages(oscMessage);
          
      });
     },
      OSCMessages: function(oscMessage){ 
        //console.log(oscMessage); 
         
      if(oscMessage.address == "/column1_color1" ){
          this.colum1color1 = 'rgb('+oscMessage.args[0]+','+oscMessage.args[1]+','+oscMessage.args[2]+')'//rgb(255, 0, 0)
          console.log("dit is de RGB waarde"+this.colum1color1 ); 
        }
     if(oscMessage.address == "/column1_color2" ){
          this.colum1color2 = 'rgb('+oscMessage.args[0]+','+oscMessage.args[1]+','+oscMessage.args[2]+')'//rgb(255, 0, 0)
          //console.log(this.rood); 
        }
      if(oscMessage.address == "/column1_position" ){
           this.column1_postion = oscMessage.args[0]
           console.log(this.column1_postion); 
         }

   
     
        

     },
   paal1down(){

       console.log("hover paal1 down")

        port.send({
         address: "/paal1",
         args: [1]
        });
    },
     paal1up(){

       console.log("hover paal1 up")

        port.send({
         address: "/paal1",
         args: [0]
        });
    },
   paal2down(){

       console.log("hover paal2 down")

        port.send({
         address: "/paal2",
         args: [1]
        });
    },
    paal2up(){
       console.log("hover paal2 up")

        port.send({
         address: "/paal2",
         args: [0]
        });
    },
 
   
  },
  
}


</script>

